@import "../variables";

.Aside {
  min-width: 75px;
  width: 30vw;
  height: 100vh;
  background-color: $white;
  box-shadow: 0 2px 10px 0 $gray-web;
  transition-duration: 600ms;

  .Aside-header {
    padding: 2em;
    color: $white;
    background-color: $tufts-blue;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition-duration: 600ms;
  }
  .Aside-switch {
    cursor: pointer;
  }
}
.colapsed {
  width: 6vw;
  background-color: $tufts-blue;
  display: flex;
  justify-content: center;

  .header-colapsed {
    padding: 1em;
    height: 30vh;
    writing-mode: vertical-rl;
    text-orientation: upright;
  }
}
@media (max-width: 750px) {
  .Aside {
    width: 100vw;
    position: absolute;
  }
  .colapsed {
    width: 17vw;
    padding: 0em;
    position: relative;
  }
  .header-colapsed {
    padding: 0em;
  }
}
