@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&display=swap);
*,*,*:before,*:after{padding:0;margin:0}.App{font-family:"Lato", sans-serif;min-height:100vh;min-width:100vw;display:flex;background-color:#fbfbfc}

@-webkit-keyframes ripple{0%{box-shadow:0px 0px 0px 1px rgba(0,0,0,0)}50%{box-shadow:0px 0px 0px 15px rgba(0,0,0,0.1)}100%{box-shadow:0px 0px 0px 15px rgba(0,0,0,0)}}@keyframes ripple{0%{box-shadow:0px 0px 0px 1px rgba(0,0,0,0)}50%{box-shadow:0px 0px 0px 15px rgba(0,0,0,0.1)}100%{box-shadow:0px 0px 0px 15px rgba(0,0,0,0)}}.aside-form{padding:2rem;color:#848a8d}.aside-form label{display:flex;padding:1rem 1rem 0rem 0;justify-content:space-between;align-items:center}.aside-form input{color:#848a8d;width:90%;padding:1rem;padding-left:0;border:none;background-color:#fbfbfc;transition-duration:200ms;border-bottom:1px solid #848a8d}.aside-form input:active,.aside-form input:focus{color:#2097f3;border-bottom:1px solid #2097f3}.aside-form input:invalid{color:#ed1c24;box-shadow:none}.aside-form .error{font-size:12px;color:#ed1c24;padding-right:1rem}.aside-form .label-error{color:#ed1c24}.aside-form .input-error,.aside-form .input-error:focus,.aside-form .input-error:active{border-bottom:1px solid #ed1c24;color:#ed1c24}.aside-form .radio-btn-container{padding-left:0;padding-bottom:1rem}.aside-form .radio-btn-container .RadioButton{margin-top:1rem;padding:0.25rem 0}.aside-form .radio-btn-container .RadioButton input[type="radio"]{display:none}.aside-form .radio-btn-container .RadioButton input[type="radio"]:checked+label:before{border-color:#2097f3;-webkit-animation:ripple 0.2s linear forwards;animation:ripple 0.2s linear forwards}.aside-form .radio-btn-container .RadioButton input[type="radio"]:checked+label:after{transform:scale(1)}.aside-form .radio-btn-container .RadioButton label{display:inline-block;height:20px;position:relative;padding:0 30px;margin-bottom:0;cursor:pointer;vertical-align:bottom}.aside-form .radio-btn-container .RadioButton label:before,.aside-form .radio-btn-container .RadioButton label:after{position:absolute;content:"";border-radius:50%;transition:all 0.3s ease;transition-property:transform, border-color}.aside-form .radio-btn-container .RadioButton label:before{left:0;top:0;width:20px;height:20px;border:2px solid #848a8d}.aside-form .radio-btn-container .RadioButton label:after{top:23%;left:6%;width:14px;height:14px;transform:scale(0);background:#2097f3}.aside-form .submit-book-button{cursor:pointer;background-color:#2097f3;padding:1rem 2rem;border:none;color:#fbfbfc;font-weight:700;font-size:1.5rem;box-shadow:2px 2px 5px 0px #848a8d;transition-duration:200ms}.aside-form .submit-book-button:hover{transform:scale(1.1)}.aside-form .submit-book-button:active{box-shadow:0 0 0 0 transparent}

.Aside{min-width:75px;width:30vw;height:100vh;background-color:#fbfbfc;box-shadow:0 2px 10px 0 #848a8d;transition-duration:600ms}.Aside .Aside-header{padding:2em;color:#fbfbfc;background-color:#2097f3;display:flex;justify-content:space-between;align-items:center;transition-duration:600ms}.Aside .Aside-switch{cursor:pointer}.colapsed{width:6vw;background-color:#2097f3;display:flex;justify-content:center}.colapsed .header-colapsed{padding:1em;height:30vh;-ms-writing-mode:tb-rl;writing-mode:vertical-rl;-webkit-text-orientation:upright;text-orientation:upright}@media (max-width: 750px){.Aside{width:100vw;position:absolute}.colapsed{width:17vw;padding:0em;position:relative}.header-colapsed{padding:0em}}

.Main{flex:1 0;height:100vh;display:grid;grid-template-columns:repeat(auto-fit, minmax(0, 400px));grid-template-rows:repeat(auto-fit, minmax(390px, 425px));overflow-y:scroll;justify-content:center;align-items:center}@media (max-width: 750px){.Main{grid-template-rows:repeat(auto-fit, minmax(475px, 1fr))}}

.Book{margin:2rem;display:flex;flex-direction:column;text-align:center;color:#848a8d;font-size:1.25rem;box-shadow:5px 5px 10px 0px #848a8d}.Book .Book-title{color:#fbfbfc;background-color:#2097f3;padding:1rem}.Book .Book-author,.Book .Book-lenght,.Book .Book-status{margin:2rem}.Book .Book-author,.Book .Book-lenght{margin-bottom:0}.Book .Book-btn{cursor:pointer;margin:1rem;font-weight:900;display:inline-block;background-color:#ed1c24;border:none;color:#fbfbfc;padding:1rem 1.5rem;box-shadow:2px 2px 5px 0px #848a8d;transition-duration:200ms}.Book .Book-btn:hover{transform:scale(1.1)}.Book .Book-btn:active{box-shadow:0 0 0 0 transparent}.Book .change{background-color:#2097f3}

