.Main {
  flex: 1 0;
  height: 100vh;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(0, 400px));
  grid-template-rows: repeat(auto-fit, minmax(390px, 425px));
  overflow-y: scroll;
  justify-content: center;
  align-items: center;
}
@media (max-width: 750px) {
  .Main {
    grid-template-rows: repeat(auto-fit, minmax(475px, 1fr));
  }
}
